.step {
  background-color: #ffffff;
  padding: 25px;
  border-radius: 10px;
}

.step__title {
  color: #1d2e5b;
  margin: 0;
}

.step__description {
  color: #444444;
  font-weight: 300;
}

.step__quantity {
  color: #1d2e5b;
}

.step__alphabet-list {
  list-style-type: none;
  padding: 0;
}

.step__alphabet-link {
  text-decoration: none;
  color: #1d2e5b;
}

.step__alphabet-link--current {
  background-color: #edeff6;
}

.step__country-list {
  list-style-type: none;
  padding: 0;
}

.step__country-link {
  text-decoration: none;
  color: #444444;
}

.step__textfield-list {
  list-style-type: none;
  padding: 0;
}
